import Sprite from "../img/Sprites.png";

const game = document.querySelector(".game__area");
const root = game.getContext("2d");

game.width = 600; //window.innerWidth
game.height = 745; //window.innerHeight

const gravity = 0.14;

class Bird{
  constructor() {
    this.position = {
      x: game.width / 2,
      y: game.height / 2,
    }
    this.positionCurrent = {
      x: this.position.x,
      y: this.position.y,
    }
    this.size = {
      width:34,
      height:24,
    }
    this.velocity = {
      x: 0,
      y: 3
    }
    this.jump = {
      height: 0,
      length: 10,
      count: 0,

    }
    this.frame = {
      current: 0,
      slow: 0,
    };
  }


  draw(){
    
    root.drawImage(
      createImage(Sprite),
      3 + 28 * this.frame.current,
      490,
      17,
      13,
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);

  }

  update(){
    this.frame.slow++;
    if(this.frame.slow > 7){
      if(this.frame.current > 1)this.frame.current = 0;
      this.frame.current++;
      this.frame.slow = 0;
    } 
    this.draw();
    this.position.y += this.velocity.y - this.jump.height;
    this.jumpBird();
    this.collisionWall();
    this.velocity.y += gravity;
  }

  collisionWall(){
    if(this.position.y + this.size.height + platform.size.height > game.height){
       cancelAnimationFrame(animationId);
    } 
    if(this.position.y - this.size.height < 0){
      cancelAnimationFrame(animationId);
    } 
  }
  jumpBird(){
    if(keys.space.pressed){
      this.jump.count++;

      this.jump.height = this.jump.length*Math.sin(Math.PI*this.jump.count/this.jump.length);
    }
    if(this.jump.count > this.jump.length) {
      this.jump.count = 0;
      this.velocity.y = 0;
      this.jump.height = 0;
      keys.space.pressed = false;
    }
  }
};

class Pipe {
  constructor(x = 0,y = 0,side = true) {
    this.position = {
      x:x,
      y:y
    }
    this.size = {
      width: 60,
      height: 700,
    }
    this.imageSideTop = {
      x: 56,
    }
    this.imageSideBottom = {
      x: 84,
    }
    this.side = side;
    if(this.side) {
      this.imageSide = this.imageSideTop;
    } else {
      this.imageSide = this.imageSideBottom;
    }
    this.scoreOne = false;
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      this.imageSide.x, 
      323, 
      26, 
      160, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
  update(){
    this.draw();
    this.position.x -= 2.2;
  }
  randomHeight(){
    return Math.floor(Math.random() * ((-120) - (-600) + 1) + (-600));
  }
};

class Platform {
  constructor(){
    this.size = {
      width: game.width,
      height: 75,
    }
    this.position = {
      x:0,
      y:game.height - this.size.height,
    }
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      292, 
      0, 
      152, 
      56, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
}

class Score {
  constructor(){
    this.position = {
      x: 10,
      y: 50
    }

    this.setup = '50px serif';
  }

  draw(scoreNumber = 0){
    root.font = this.setup;
    root.fillText(scoreNumber, this.position.x, this.position.y);
  }
}

//create bird
let bird = new Bird();
//create pipe 
let pipesPair = [];
let scoreNumber = 0;
let score = new Score();
let platform = new Platform();

// to check if a user pressed the space bar 
let keys = {
  space: {
    pressed: false,
  },
}; 


// function for creating image
function createImage(imageScr){
  const image = new Image();
  image.src = imageScr;
  return image;
}



// Collision between Bird and Pipes
function ditectionCollisionBirdAndPipes(pipeCollision) {
   
  if( bird.position.x + bird.size.width >= pipeCollision.position.x
    && bird.position.x <= pipeCollision.size.width + pipeCollision.position.x
    && bird.position.y + bird.size.height >= pipeCollision.position.y
    && bird.position.y <= pipeCollision.size.height + pipeCollision.position.y) {
  
    return true;

  } else {

    return false;
    
  }
}

// function for creating pairs of pipes 
function createPairOfPipes() {
  let min = -630,
      max = -200;

  let pipes = [];
  let randomY = Math.floor(Math.random() * ((max) - (min) + 1) + min);
  pipes[0] = new Pipe(game.width + 600,randomY);
  pipes[1] = new Pipe(game.width + 600, randomY + 800, false);
  pipesPair.push(pipes);
}
setInterval(()=>{
  createPairOfPipes();
}, 2500);



//main function
let animationId;

function animation(){
  animationId = requestAnimationFrame(animation);
  root.drawImage(createImage(Sprite), 0, 0, 144, 256, 0, 0, game.width, game.height);
  platform.draw();
  pipesPair.forEach((pipePair)=>{
    pipePair.forEach( function(pipe) {
      pipe.update();
      if(ditectionCollisionBirdAndPipes(pipe)){
        cancelAnimationFrame(animationId);
      }
    });
    if(pipePair[0].position.x +  pipePair[0].size.width < -400){
      pipesPair.splice(0,1);
    }
    if(pipePair[0].position.x +  pipePair[0].size.width < game.width/2
      && pipePair[0].scoreOne == false){
      pipePair[0].scoreOne = true;
      scoreNumber++;
    }
  });
  bird.collisionWall();
  score.draw(scoreNumber);
  root.save();
  bird.update();
}



animation();


document.addEventListener('keydown',({keyCode}) => {
  switch (keyCode) {
    case 32:
      keys.space.pressed = true;
      break;
  }
});

